import React, {Component} from 'react';
import {connect} from 'react-redux';
import {push} from 'react-router-redux';
import PropTypes from 'prop-types';
import {actionCreator} from '../../../actions';
import * as types from '../../../constants/actiontypes';
import RaisedButton from 'material-ui/RaisedButton';
import {Table, TableBody, TableHeader, TableHeaderColumn, TableRow, TableRowColumn, TableFooter} from 'material-ui/Table';
import Checkbox from 'material-ui/Checkbox';
import QueueAnim from 'rc-queue-anim';
import {FetchHelper} from '../../../helpers/fetchhelper';


const styles = {
  checkbox: {
    maxWidth: 250,
    marginBottom: 16
  }
};

class SearchHouse extends Component {

  selectedRowNo = 0;

  componentWillMount() {
    this.props.init();
  }

  search = (e) => {
    this.props.clickSearch(this.refs.input_house_name.value, this.props.hasModel);
  }

  detail = (e) => {
    const houseId = this.props.results[this.selectedRowNo].houseId;
    this.props.clickDetail(houseId);
  }

  changeHasModel = (e) => {
    this.props.changeHasModel(e.target.checked);
  }

  select = (rowNumber) => {
    this.selectedRowNo = rowNumber;
  }

  render() {
    const {match, location} = this.props;

    return (
      <section className="container-fluid with-maxwidth chapter">
        <QueueAnim type="bottom" className="ui-animate">

          <div key="1">
            <form role="form">
              <article className="article">
                <h2 className="article-title"></h2>
                <div className="box box-default">
                  <div className="box-body padding-xl">

                    <div className="form-group row">
                      <label className="col-md-2 control-label"></label>
                      <div className="col-md-10">
                        <input
                          type="text" className="form-control" maxLength={255} placeholder="255" ref="input_house_name"/>
                      </div>
                    </div>

                    {/* <div className="form-group row">
                      <label className="col-md-2 control-label">CG</label>
                      <div className="col-md-10">
                        <Checkbox style={styles.checkbox} ref="input_has_model" onCheck={(event) => this.changeHasModel(event)} />
                      </div>
                    </div> */}
                    
                    <div className="form-group row">
                      <div className="col-md-2"></div>
                      <div className="col-md-10">
                        <RaisedButton label="" className="btn-w-md" onClick={(event) => this.search(event)} />
                      </div>
                    </div>

                  </div>
                </div>
              </article>
            </form>

            <div className="form-group row">
              <div className="col-md-10"></div>
              <div className="col-md-2">
                <RaisedButton label="" className="btn-w-md" onClick={(event) => this.detail(event)} />
              </div>
            </div>

            <div className="box box-default table-box table-flip-scroll mdl-shadow--2dp">
              <Table onCellClick={(rowNumber, columnNumber, event) => this.select(rowNumber, columnNumber, event)}>
                <TableHeader displaySelectAll={false}>
                  <TableRow>
                    <TableHeaderColumn>ID</TableHeaderColumn>
                    <TableHeaderColumn></TableHeaderColumn>
                    <TableHeaderColumn>ID</TableHeaderColumn>
                    <TableHeaderColumn></TableHeaderColumn>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {this.props.results.map((item, index) => {
                    return (
                      <TableRow key={index}>
                        <TableRowColumn>{item.houseId}</TableRowColumn>
                        <TableRowColumn>{item.houseCode}</TableRowColumn>
                        <TableRowColumn>{item.makerId}</TableRowColumn>
                        <TableRowColumn>{item.makerName}</TableRowColumn>
                      </TableRow>
                    )
                  })}
                </TableBody>
              </Table>
            </div>
         
          </div>
        </QueueAnim>
      </section>
    );
  }
}
SearchHouse.propTypes = {
  clickSearch: PropTypes.func,
  clickDetail: PropTypes.func,
  changeHasModel: PropTypes.func,
  clickRow: PropTypes.func,
  selectedRowNo: PropTypes.number,
};

const mapStateToProps = state => ({
  houseName: state.searchHousesetting.houseName || '',
  hasModel: state.searchHousesetting.hasModel || {},
  results: state.searchHousesetting.results || [],
});

const mapDispatchToProps = dispatch => ({
  init: () => {
    new FetchHelper().execute('/auth/check', 'post', {}, () => {
      dispatch(actionCreator(types.INIT_HOUSE));        
    });
  },
  clickSearch: (value, checked) => {
    let params = {
      name: value,
      has: checked
    }

    new FetchHelper().execute('/search/house', 'POST', params, 
      (result) => {
        let makers = [];
        result.map((maker) => {
          makers.push({ 
            houseId: maker.house_id,
            houseCode: maker.house_code,
            makerId: maker.maker_id, 
            makerName: maker.maker_name,
          });
        });
        dispatch(actionCreator(types.SEARCH_HOUSE, [makers]));
      }
    );
    
  },
  clickDetail: (houseid) => {
    dispatch(push(`/app/edithouse/${houseid}/searchhouse`));
  },
  changeHasModel: (value) => {
    dispatch(actionCreator(types.CHANGE_HAS_MODEL, [value]));
  },
  clickRow: (value) => {
    dispatch(actionCreator(types.CLICK_HOUSE_ROW, value));
  }
});

module.exports = connect(mapStateToProps, mapDispatchToProps)(SearchHouse);


